export const useCopy = () => {
    const copy = (url: string) => {
      const input = document.createElement("input") // input
      input.value = url // 
      document.body.appendChild(input)  // 
      input.select() // 
      document.execCommand("Copy") // 
      document.body.removeChild(input) // 
    }
    return {
      copy,
    }
  }